const container = document.getElementById('root'); // find root tag
const ajax = new XMLHttpRequest(); // ajax Ï∂úÎ†• Í≤∞Í≥º Î∞òÌôò
const content = document.createElement('div');
const URL_ADDR = 'https://api.hnpwa.com/v0/news/1.json';
const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json'; // Ìï¥Îãπ ÏΩòÌÖêÏ∏†Ïùò url

function getData(method, url, async) {
    ajax.open(method, url, async); // ÎèôÍ∏∞ or ÎπÑÎèôÍ∏∞ Î∞©ÏãùÏúºÎ°ú ÏÑúÎ≤Ñ ÏöîÏ≤≠ Í∞í Ï≤òÎ¶¨
    ajax.send(); // Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÏ†∏Ïò§Îäî ÏûëÏóÖ
}

getData('GET', URL_ADDR, false);

const newsFeed = JSON.parse(ajax.response); // json Îç∞Ïù¥ÌÑ∞ Í∞ùÏ≤¥ Î≥ÄÌôò ÌõÑ Î¶¨ÌÑ¥
const ul = document.createElement('ul'); // ul tag ÏÉùÏÑ±

window.addEventListener('hashchange', function() {
    console.log('hash changed')
    console.log(location.hash); // location Í∞ùÏ≤¥Ïùò hash Í∞í ÌôïÏù∏ #3029303929 ÏôÄ Í∞ôÏùÄ Î∞©ÏãùÏúºÎ°ú Í∞í Î∞òÌôò

    const id = location.hash.substr(1); // # Ïù¥ÌõÑÏùò ÎÇ¥Ïö© Ï†ÄÏû•
    getData('GET', CONTENT_URL.replace('@id', id), false);

    const newsContent = JSON.parse(ajax.response);
    const title = document.createElement('h1');

    title.innerHTML = newsContent.title;
    content.appendChild(title);
    console.log(newsContent);
});

for (let i = 0; i < 10; i++) {
    const div = document.createElement('div');

    div.innerHTML = 
    `
    <li>
        <a href="#${newsFeed[i].id}">
            ${newsFeed[i].title} (${newsFeed[i].comments_count})
        </a>
    </li>
    `
    // ul.appendChild(div.children[0]);
    ul.appendChild(div.firstElementChild);
}

container.appendChild(ul);
container.appendChild(content);